.video {
  background-color: #131314;
  color: #ffffff;
  width: 100%;
  box-sizing: border-box;
  text-align: center;
  padding-top: 120px;
  padding-bottom: 480px;
  position: relative; }
  @media (max-width: 1023px) {
    .video {
      padding-top: 70px;
      padding-bottom: 237px; } }
  @media (max-width: 767px) {
    .video {
      padding-top: 84px;
      padding-bottom: 163px;
      padding-right: 16px;
      padding-left: 16px; } }

.video__title {
  font-size: 30px;
  line-height: 30px;
  font-weight: 600;
  margin-top: 0; }
  @media (max-width: 1023px) {
    .video__title {
      font-size: 24px; } }

.video__intro {
  width: 340px;
  margin: 20px auto;
  font-size: 16px;
  font-weight: 300;
  line-height: 28px;
  color: #ffffff;
  opacity: 0.7;
  margin-bottom: 0; }
  @media (max-width: 1023px) {
    .video__intro {
      width: 429px; } }
  @media (max-width: 767px) {
    .video__intro {
      width: 100%; } }

.video__player {
  width: 929px;
  height: 522px;
  box-sizing: border-box;
  position: absolute;
  top: 282px;
  left: 50%;
  transform: translateX(-50%); }
  @media (max-width: 1023px) {
    .video__player {
      width: 503px;
      height: 282px;
      top: 216px; } }
  @media (max-width: 767px) {
    .video__player {
      width: 288px;
      height: auto; } }

.video__player-button {
  border: none;
  padding: 0;
  background: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }
  @media (max-width: 1023px) {
    .video__player-button {
      width: 90px;
      height: 90px; } }
  @media (max-width: 767px) {
    .video__player-button {
      width: 65px;
      height: 65px; } }

.video__player-icon {
  width: 100%;
  height: auto; }
